<template>
	<li class="todolist-item"
		:class="{active:data.istop}"
		v-if="(show === data.isdone)">
		<span class="dot"
			v-if="!data.isdone"
		>
		</span>
		<p class="todolist-item-content"
			v-show="!edit"
			@click="itemEdit"
		>
			{{data.content}}
		</p>
		<input type="text" class="todolist-item-input"
			v-model="data.content"
			v-show="edit"
			@blur="itemEditDone(data,$event)"
			@keyup.enter="$event.target.blur"
		>
		<div class="todolist-item-tools">
			<BaseButton type="success"
				v-if="navIndex === 0"
				@click="itemSwitchTodoAndDone(data)">完成</BaseButton>
			<BaseButton type="info"
				v-if="navIndex === 0"
				@click="itemToTop(data)">置顶</BaseButton>
			<BaseButton type="info"
				v-if="navIndex === 1"
				@click="itemSwitchTodoAndDone(data)">撤回</BaseButton>
			<BaseButton type="warning"
				@click="itemDelete(data)">删除</BaseButton>
		</div>
	</li>
</template>

<script>
import BaseButton from '../../components/BaseComponent/BaseButton.vue'
export default {
	name: 'TodolistItem',
	components: {
		BaseButton
	},
	props: {
		data:Object,
		navIndex: Number,
	},
	data() { 
		return {
			edit: false,
		}
	},
	computed: {
		show(){
			return this.navIndex === 0 ? false : true
		}
	},
	methods: {
		// * 编辑
		itemEdit(event){
			this.edit = true
			let inputElement = event.target.nextSibling
			this.$nextTick(()=>{ // * 需要马上执行
				inputElement.focus();
			})
		},
		// * 编辑完成
		itemEditDone(data,event){
			this.edit = false
			let value = event.target.value
			// TODO 判断 输入框的 value 不变的情况
			if(value === '') return
			this.$emit('itemEditDone',data)
		},
		// * 置顶
		itemToTop(data){
			data.istop = !data.istop
			this.$emit('itemToTop',data)
		},
		// * 切换 todo & done
		itemSwitchTodoAndDone(data){
			data.isdone = !data.isdone
			data.istop = false
			this.$emit('itemSwitchTodoAndDone',data)
		},
		// * 删除
		itemDelete(data){
			this.$emit('itemDelete',data)
		}
	},
}
</script>

<style lang="less">
.todolist-item{
	display: flex;
	align-items: center;
	margin-bottom: 10px;
	font-size: 16px;
	.dot {
		width: 10px;
		height: 10px;
		border-radius: 50%;
	}
	&-content,
	&-input {
		margin-left: 20px;
		line-height: 34px;
		flex-grow: 1;
	}
	&-content {
		cursor: text;
	}
	&-tools {
		margin-left: 10px;
	}
} 
</style>