<template>
	<div class="theme-default" id="theme-wrapper">
		<div id="todo" class="todo">
			<TodoHeader @tab-theme="tabTheme"
				@tabNavItems="tabNavItems"
			/>
			<TodoCotent :navIndex="navIndex"/>
		</div>
	</div>
</template>

<script>
import TodoHeader from './TodoHeader.vue'
import TodoCotent from './TodoCotent.vue'
import store from './store.js'

// import './theme/index.js'
export default {
	name: 'Todo',
	data() { 
		return {
			themeName: '',
			navIndex: 0,
		}
	},
	components:{
		TodoHeader,
		TodoCotent
	},
	methods: {
		tabTheme(name){
			this.themeName = `theme-${name}`
			let element = document.getElementById('theme-wrapper')
			element.className = this.themeName
			store.save('theme-name',this.themeName)
		},
		tabNavItems(index){
			this.navIndex = index
		}
	},
	mounted() {
		this.themeName = store.get('theme-name') || 'theme-default'
		let element = document.getElementById('theme-wrapper')
		element.className = this.themeName
	},
}
</script>
<style lang="less">
@import "./theme/index.less";
@import "../../assets/css/custom.less";
@padding-vertical-base: 15px;
@padding-vertical-base: 20px;
.todo{
	width: 900px;
	height: 610px;
	margin: 30px auto 0;
	padding: @padding-vertical-base @padding-vertical-base;
	h2 {
		color:@primary-color;
	}
}
</style>