<template>
    <button
        @click="handelClick"
        :disabled="this.disabled"
        :class="btnClasses">
        <i></i>
        <span><slot></slot></span>
    </button>
</template>

<script>

// 定义前缀
import {oneOf} from '../utils/assits.js'
const prefixCls = 'base-btn';

export default {
    name: 'BaseButton',
    props: {
        type: {
            validator(value){
                return oneOf(value,['default', 'primary', 'dashed', 'text', 'info', 'success', 'warning', 'error'])
            },
            default: 'default'
        },
        shape: {
            validator(value){
                return oneOf(value,['circle','circle-only'])
            }
        },
        size: {
            validator(value){
                return oneOf(value,['default','large','small'])
            },
            default: 'default'
        },
        icon: {
            validator(value){
                return oneOf(value,['sanjiao1','service','menu','you','triangle-up','phone'])
            },
        },
        long: {
            type: Boolean,
            default: false
        },
        disabled: Boolean
    },
    data() { 
        return {

        }
    },
    methods: {
        handelClick(event){
            this.$emit('click',event)
        }
    },
    computed: {
        btnClasses() {
            return [
                `${prefixCls}`, 
                // `${prefixCls}-default`, 
                [`${prefixCls}-${this.type}`],
                {
                    [`${prefixCls}-${this.shape}`]: !!this.shape,
                    [`${prefixCls}-${this.size}`]: !!this.size,
                    [`iconfont icon-${this.icon}`]: !!this.icon,
                    [`${prefixCls}-long`]: this.long,
                }
            ]
        },
    },
 }
</script>

<style lang="less" scoped>
@import url(./index.less); // * custom.less
@btn-prefix-cls: base-btn;
.@{btn-prefix-cls} {
    display: inline-block;
    padding: 6px 12px;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.42857143;
    color: #333;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-image: none;
    border: 1px solid transparent;
    border-radius: 4px;
    box-sizing: content-box;
    &:hover {
        color: @text-color;
        text-decoration: none;
    }
    &-default {
        color: @text-color;
        background-color: #fff;
        border-color: #333;
    }
    &-default:hover {
        color: @text-color-light;
        background-color: #e6e6e6;
        border-color: #adadad;
    }
    &-primary {
        color: @primary-color;
        background-color: #fff;
        border-color: @primary-color;
    }
    &-primary:hover {
        color: @primary-color-light;
        border-color: @primary-color-light;
    }
    &-dashed {
        color: @dashed-color;
        background-color: #fff;
        border-style: dashed;
        border-color: @dashed-color;
    }
    &-dashed:hover {
        color: @dashed-color-dark;
        background-color: #fff;
        border-color: @dashed-color-dark;
    }
    &-text {
        color: @text-color;
        background-color: #fff;
        border-color: @text-color;
    }
    &-text:hover {
        color: @text-color-light;
        border-color: @text-color-light;
    }
    &-info {
        color: @info-color;
        background-color: #fff;
        border-color: @info-color;
    }
    &-info:hover {
        color: @info-color-light;
        border-color: @info-color-light;
    }
    &-success {
        color: @success-color;
        background-color: #fff;
        border-color: @success-color;
    }
    &-success:hover {
        color: @success-color-light;
        border-color: @success-color-light;
    }
    &-warning {
        color: @warning-color;
        background-color: #fff;
        border-color: @warning-color;
    }
    &-warning:hover {
        color: @warning-color-light;
        border-color: @warning-color-light;
    }
    &-error {
        color: @error-color;
        background-color: #fff;
        border-color: @error-color;
    }
    &-error:hover {
        color: @error-color-light;
        border-color: @error-color-light;
    }
    &-large {
        font-size: 16px;
        padding: 6px 15px 7px;
    }
    &-small {
        font-size: 12px;
        padding: 2px 7px;
    }
    &-circle {
        height: 32px;
        border-radius: 16px;
    }
    &-circle-only {
        width: 32px;
        height: 32px;
        padding: 0;
        border-radius: 50%;
    }
    &[disabled] {
        cursor: not-allowed;
        background: @disabled-bg;
        border-color: @border-color-disabled;
        &:hover {
            background: @disabled-bg;
            border-color: @border-color-disabled; 
        }
    }
    &-long {
        width: 100%;
    }
}
</style>