<template>
    <nav class="todo-header-nav" id="todo_nav">
		<button class="todo-header-nav-item"
            type="primary"
            :class="{'active': navIndex === index}"
            v-for="(item,index) in navs"
            :key="index"
            @click="handleClick(index)">
            {{item}}
        </button>
    </nav>
</template> 

<script>
export default {
    name: 'TodoHeaderNav',
    data() { 
        return {
            navs: ['Todo','Done'],
            navIndex: 0,
        }
    },
    methods: {
        handleClick(index){
            this.navIndex = index;
            this.$emit('tabNavItems',this.navIndex)
        }
    },
}
</script>

<style lang="less">
@import "../../assets/css/custom.less";
.todo-header-nav{
    display: flex;
    .todo-header-nav-item {
        display: inline-block;
        padding: 6px 12px;
        margin-bottom: 0;
        font-size: 14px;
        font-weight: 400;
        line-height: 1.42857143;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        -ms-touch-action: manipulation;
        touch-action: manipulation;
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        background-image: none;
        background-color: transparent;
        border: 1px solid;
        border-radius: 4px;
        box-sizing: content-box;
    }
}
</style>