<template>
    <div class="base-input-wraper">
        <span class="prepend" v-if="!!prepend">{{prepend}}</span>
        <i class="base-input-icon"
            v-if="icon"
            :class="inputIcon"
            @click="iconClick"></i>
        <input
            :value="currentValue"
            :placeholder="placeholder"
            :disabled="disabled"
            :readonly="readonly"
            :maxlength="maxlength"
            :type="inputType"
            :class="inputClasses"
            @input="handleInput"
            @keyup.enter="handleEnter"
            @focus="handleFocus"
            @blur="handleBlur">
        <span class="append" v-if="append">{{append}}</span>
    </div>
</template>

<script>
import {oneOf} from '../utils/assits.js' // 工具
const prefixCls = 'base-input'; // * 前缀
export default {
    name: 'BaseInput',
    data() { 
        return {
            currentValue: this.value,
        }
    },
    props: {
        type: {
            validator(value){
                return oneOf(value,['text','password'])
            },
            default: 'text'
        },
        value: { // ? 父组件需要用 .sync 绑定,才能实现双向绑定  已经用 v-model, .sync 不知道怎么用
            type: [String,Number],
            default: ''
        },
        size: {
            validator(value){ // DONE 没有 base-input-default 这个类,传进来在 inputClasses 中处理一下
                return oneOf(value,['large','default','small'])
            },
            default: 'default'
        },
        placeholder: {
            type: String,
            default: ''
        },
        icon: {
            validator(value){
                return oneOf(value,['sanjiao1','service','menu','you','triangle-up','phone'])
            }
        },
        prepend: String,
        append: String,
        // DONE 事件 input[number]
        number: {
            type: Boolean,
            default: false
        },
        // DONE 在 input 事件中处理
        maxlength: Number, // * 绑定数字 :maxlength="10" 必须用 v-bind 绑定,不然绑定的是字符串
        disabled: Boolean,
        readonly: Boolean,
    },
    computed: {
        inputClasses(){
            return [
                `${prefixCls}`,
                {
                    [`${prefixCls}-${this.size}`]: this.size !== 'default'
                }
            ]
        },
        inputType(){
            return `${this.type}`
        },
        inputIcon(){
            return `iconfont icon-${this.icon}`
        },
        
    },
    methods: {
        handleInput(event){
            let value = event.target.value
            if(value.length && value.length > this.maxlength){
                event.target.value = value.slice(0,this.maxlength)
            }
            if (this.number && value !== ''){ // * 处理 number prop
                if(Number.isNaN(Number(value))){ // * NaN 自己不等于自己
                    value = Number.isNaN(parseFloat(value)) ? '' : parseFloat(value)
                }else{
                    value = Number(value)
                }
            }
            this.$emit('on-change',event) // * 数据变化时,即 input 标签的 input 事件
            this.$emit('input', value)
        },
        iconClick(event){
            this.$emit('on-click',event)
        },
        handleFocus(event){
            this.$emit('on-focus',event)
        },
        handleBlur(event){
            this.$emit('on-blur',event)
        },
        handleEnter(event){
            this.$emit('on-enter',event)
        }
    },
 }
</script>

<style lang="less" scoped>
@import url(./index.less); // * custom.less
@input-prefix-cls: base-input;

.@{input-prefix-cls}-wraper {
    position: relative;
    display: inline-table;
    vertical-align: middle;
    width: 300px; // * input 的默认宽度,可以在父组件的 style->width 中修改宽度
    box-sizing: border-box;
    .base-input-icon {
        position: absolute;
        z-index: 10;
        right: 18px;
        top: 50%;
        margin-top: -8px;
        cursor: pointer;
    }
    .prepend,
    .append {
        display: table-cell;
        text-align: center;
    }
    .@{input-prefix-cls} {
        display: inline-block;
        width: 100%;
        position: relative;
        padding-right: 32px;
        height: 32px; // * input 的默认高度, size= [large,default,small]
        line-height: 1.5;
        padding: 4px 7px;
        font-size: 12px;
        border: 1px solid #d7dde4;
        border-radius: 4px;
        color: #657180;
        background-color: #fff;
        background-image: none;
        cursor: text;
        transition: border 0.2s;
        &[disabled] {
            cursor: not-allowed;
            background: @disabled-bg;
            border-color: @border-color-disabled;
            &:hover {
                background: @disabled-bg;
                border-color: @border-color-disabled;
                box-shadow: none;
            }
        }
        &-large {
            height: 40px;
        }
        &-small {
            height: 24px;
        }
        &:focus,
        &:hover {
            box-shadow: 0 0 0 2px rgba(51,153,255,.2);
            border-color: #5cadff;
        }
    }
}
</style>