<template>
	<div class="todo-header-theme_switcher" id="theme_switcher">
		<span
			v-for="(item, index) in themes" :key="index"
			:class="[{active:index === themeIndex},dotClasses]"
			:style="{'background-color':themes[index].color,'border-color':themes[index].color}"
			@click="tabTheme(index)">
		</span>
	</div>
</template>

<script>
import store from './store.js'

export default {
	name: 'TodoHeaderThemeSwitcher',
	data() { 
		return {
			themes: [
				{
					name: 'default',
					color: '#30c2ad'
				},
				{
					name: 'pink',
					color: '#e7939f'
				},
				{
					name: 'dark',
					color: '#1e1e1e'
				}
			],
			themeIndex: 0,
		}
	},
	created() {
		let themeName = store.get('theme-name')
		themeName = themeName.length === 0 ? 'theme-default' : themeName
		let name = themeName.split('-')[1]
		this.themes.forEach((item,index)=>{
			if(item.name === name){
				this.themeIndex = index
				return
			}
		})
	},
	computed: {
		dotClasses(){
			return [
				'todo-header-theme_switcher-dot',
			]
		},
		dotStyles(){
			let color = this.themes[this.themeIndex].color
			return {
				background: color,
				borderColor: color
			}
		},
	},
	methods: {
		tabTheme(index){
			this.themeIndex = index;
			let name = this.themes[index].name
			this.$emit('tab-theme',name)
		}
	},
}
</script>

<style lang="less">
.todo-header-theme_switcher {
	display: flex;
	.todo-header-theme_switcher-dot {
		width: 20px;
		height: 20px;
		padding: 4px;
		margin-left: 5px;
		border:2px solid;
		border-radius: 4px;
		opacity: .3;
		&.active {
			opacity: 1;
			background-clip: content-box;
		}
		&:hover {
			opacity: 1;
		}
	}
}
</style>