<template>
	<div class="todolist-wrapper">
		<div class="todolist-new_items">
			<BaseInput size="large"
				style="width:400px;"
				@on-enter="itemNew"
			/>
		</div>
		<ul class="todolist" id="todolist">
			<!-- // * 置顶项 -->
			<TodoListItem
				v-for="(item) in todoDataTopShow" :key="item.key"
				:data="item"
				:navIndex="navIndex"
				@itemEditDone="itemEditDone"
				@itemToTop="itemToTop"
				@itemSwitchTodoAndDone="itemSwitchTodoAndDone"
				@itemDelete="itemDelete"
			/>
			<!-- // * 非置顶项 -->
			<TodoListItem
				v-for="(item) in todoDataShow" :key="item.key"
				:data="item"
				:navIndex="navIndex"
				@itemEditDone="itemEditDone"
				@itemToTop="itemToTop"
				@itemSwitchTodoAndDone="itemSwitchTodoAndDone"
				@itemDelete="itemDelete"
			/>
		</ul>
	</div>
</template>

<script>
import TodoListItem from './TodoListItem.vue'
import store from './store.js'

let todoData = store.get('tododata')

export default {
	name: 'TodoList',
	props: {
		navIndex: Number,
	},
	data() { 
		return {
			todoData: [],
		}
	},
	components: {
		TodoListItem
	},
	created() {
		// 初始化数据用,用完注释
		// this.getData();
		// * 初始化数据
		this.todoData = store.get('tododata')
		// ! 调整测试数据用,将所有已完成事项的 istop 属性归 false
		// this._initData()
	},
	computed: {
		todoDataShow(){
			return this.todoData.filter(item=>{
				return !item.istop
			})
			.filter(item=>{
				return this.navIndex === 0 ? !item.isdone : item.isdone
			})
		},
		// TODO 置顶项目前是按照原数组的顺序排序,而不是"后来居上" 给 localStorage 新增 'top-sort' 数组,记录置顶项的顺序,用来对 todoDataShow 排序
		todoDataTopShow(){
			return this.todoData.filter(item=>{
				return item.istop
			})
			.filter(item=>{
				return this.navIndex === 0 ? !item.isdone : item.isdone
			})
		}
	},
	methods: {
		_initData(){
			this.todoData.forEach(item=>{
				if(item.isdone){
					if(item.istop) item.istop = false
				}
			})
			store.save('tododata',this.todoData)
		},
		getData(){
			//请求接口
			this.axios.get('/todoinfo')
			.then(({data})=>{
				if(data.error === 0){
					store.save('tododata',data.data)
				}else{
					store.save('tododata',[])
				}
			});
		},
		// * 处理: 置顶 编辑 切换 删除
		processingData(data,eventName){
			this.todoData.forEach((item,index,arr)=>{
				if(item.id === data.id){
					switch(eventName){
						case 'itemToTop': { // * 置顶
							item.istop = data.istop
							break;
						}
						case 'itemEditDone': { // * 编辑完成
							item.content = data.content
							break;
						}
						case 'itemSwitchTodoAndDone': {// * 切换 todo & done
							[item.isdone,item.istop] = [data.isdone,data.istop]
							break; 
						}
						case 'itemDelete': { // * 删除
							arr.splice(index,1)
							break;
						}
					}
					return
				}
			})
			store.save('tododata',this.todoData)
		},
		newData(value){
			let item = {}
			item.content = value
			item.istop = false
			item.isdone = false
			item.publishtime = new Date().toLocaleString
			item.id = Math.random().toString(36).slice(2)
			return item
		},
		// * 新建
		itemNew(event){
			let todolistElement = document.getElementById('todolist')
			let value = event.target.value
			if(value === '') return
			let item = this.newData(value);
				this.todoData.push(item)
			// * 清空输入框,滚动到底部
			store.save('tododata',this.todoData)
			this.$nextTick(()=>{
				todolistElement = document.getElementById('todolist')
			event.target.value = ''
			todolistElement.scrollTop = todolistElement.scrollHeight
			})
		},
		itemEditDone(data){
			this.processingData(data,'itemEditDone')
		},
		itemToTop(data){
			this.processingData(data,'itemToTop')
		},
		itemSwitchTodoAndDone(data){
			this.processingData(data,'itemSwitchTodoAndDone')
		},
		itemDelete(data){
			this.processingData(data,'itemDelete')
		}
	},
}
</script>

<style lang="less">
.todolist-wrapper {
	width: 100%;
		// ! 2个方法兼容 firefox 和 ie,但chrome 下也得一样用法
		// padding-left: 17px;
		// border-right: 17px solid #f9f8e6;
	overflow: hidden;
	.todolist-new_items {
		display: flex;
		justify-content: center;
		margin-top: 10px;
	}
	.todolist{
		width: 830px;
		margin-top: 10px;
		max-height: 370px;
		overflow-x: hidden;
		overflow-y: auto;
		&::-webkit-scrollbar { width: 0 !important } // chrome safari
		
	}
}
</style>